// スムーススクロール

var easeInOutCubic = function(t, b, c, d) {
  t /= d / 2;
  if (t < 1)
    return c / 2 * t * t * t + b;
  t -= 2;
  return c / 2 * (t * t * t + 2) + b;
};

(function() {

  var ss = document.querySelectorAll('a[href^="#"]');

  if (ss.length > 0) {
    for (var i = 0; i < ss.length; i++) {
      ss[i].addEventListener('click', smoothscroll, false);
    }
  }

  function smoothscroll(e) {
    e.preventDefault();

    // アンカーを取得
    var href = this.getAttribute('href');
    href = href.split('#');
    href = href[1];

    // アンカーの縦位置取得
    var bodyScrollTop = document.documentElement.scrollTop || document.body.scrollTop;
    var target = document.getElementById(href);
    var targetTop = target.getBoundingClientRect().top + bodyScrollTop;

    // if( getW() > 750){
    // 	targetTop = targetTop -72;
    // 	if(targetTop < 0){
    // 		targetTop = 0;
    // 	}
    // }

    var contentH = document.body.getBoundingClientRect().bottom;

    var winH = window.innerHeight;
    var y = bodyScrollTop || 0;

    var FPS = 30;
    var TIME = 500;

    var t = 0; // t : 時間(進行度)
    var b = y; // b : 開始の値(開始時の座標やスケールなど)
    var c = targetTop - y; // c : 開始と終了の値の差分
    var d = TIME / FPS; // d : Tween(トゥイーン)の合計時間

    var scrollable = true;

    function scroll() {
      if (!scrollable) {
        removeCancelEvent();
        return false;
      }
      y = easeInOutCubic(t, b, c, d);
      window.scrollTo(0, y);
      if (t < d) {
        t++;
        window.setTimeout(scroll, 1000 / FPS);
      } else {
        removeCancelEvent();
      }
    }

    // マウスホイールのイベントを検知。
    window.addEventListener('mousewheel', cancel, false);
    function cancel() {
      scrollable = false;
    }
    function removeCancelEvent() {
      window.removeEventListener('mousewheel', cancel, false);
    }
    scroll();
  }

})();

// メニュー開閉

const $menuBg = document.querySelector('.nav-bg');
const $menuL = document.querySelector('.nav__local');
const $menuG = document.querySelector('.nav__global');
const $menuOpenL = document.querySelectorAll('.button-menuL');
const $menuOpenG = document.querySelectorAll('.button-menuG');

if ($menuOpenL.length > 0) {
  for (var i = 0; i < $menuOpenL.length; i++) {
    $menuOpenL[i].addEventListener('click', menuOpenL, false);
  }
}

if ($menuOpenG.length > 0) {
  for (var i = 0; i < $menuOpenG.length; i++) {
    $menuOpenG[i].addEventListener('click', menuOpenG, false);
  }
}

var $menuClose = document.querySelectorAll('.nav-bg,.nav__close');

if ($menuClose.length > 0) {
  for (var i = 0; i < $menuClose.length; i++) {
    $menuClose[i].addEventListener('click', menuClose, false);
  }
}

function menuOpenL() {
  if(!$menuOpenL[0].classList.contains('opened')){
    $menuL.classList.add('block');
    $menuBg.classList.add('block');
    $menuG.classList.remove('show');
    $menuOpenL[0].classList.add('opened');
    setTimeout(function() {
      $menuL.classList.add('show');
      $menuBg.classList.add('show');
    }, 50);
    setTimeout(function() {
      $menuG.classList.remove('block');
    }, 300);
  }else{
    menuClose();
  }
}

function menuOpenG() {
  if(!$menuOpenG[0].classList.contains('opened')){
    $menuG.classList.add('block');
    $menuBg.classList.add('block');
    $menuL.classList.remove('show');
    $menuOpenG[0].classList.add('opened');
    setTimeout(function() {
      $menuG.classList.add('show');
      $menuBg.classList.add('show');
    }, 50);
    setTimeout(function() {
      $menuL.classList.remove('block');
    }, 300);
  }else{
    menuClose();
  }
}

function menuClose() {
  $menuL.classList.remove('show');
  $menuG.classList.remove('show');
  $menuBg.classList.remove('show');
  setTimeout(function() {
    $menuL.classList.remove('block');
    $menuG.classList.remove('block');
    $menuBg.classList.remove('block');
    $menuOpenL[0].classList.remove('opened');
    $menuOpenG[0].classList.remove('opened');
  }, 300);
  // isPageScroll = true;
  // setTimeout(function() {
  //   isPageScroll = false;
  // }, 1000)
}

function menuReset() {
  $menuOpen[0].classList.remove('open');
  $menuHeader.classList.remove('hide2');
  $menu.classList.remove('show');
  $menu.classList.remove('block');
}

// カルーセル

const $accordion = document.querySelectorAll('.accordion__title');

if ($accordion.length > 0) {
  for (var i = 0; i < $accordion.length; i++) {
    $accordion[i].addEventListener('click',
    function(event){
      if(event.target.parentNode.classList.contains('open')){
        event.target.parentNode.classList.remove('open');
      }else{
        event.target.parentNode.classList.add('open');
      }
    }, false);
  }
}


// モダル
const $modal = document.querySelector('.confirm');
const $modal2 = document.querySelector('.confirm.show2');
const $modalClick = document.querySelectorAll('.modal-open');

if ($modalClick.length > 0) {
  for (var i = 0; i < $modalClick.length; i++) {
    $modalClick[i].addEventListener('click', function(event){
      const target = this.dataset.modal;
      document.getElementById(target).classList.add('show');
      setTimeout(function() {
        document.getElementById(target).classList.add('show2');
      }, 50);
    }, false);
  }
}

var $modalClickClose = document.querySelectorAll('.confirm__bg,.confirm__close');

if ($modalClickClose.length > 0) {
  for (var i = 0; i < $modalClickClose.length; i++) {
    $modalClickClose[i].addEventListener('click', function(event){
      $modal.classList.remove('show2');
      setTimeout(function() {
        $modal.classList.remove('show');
      }, 200);
    }, false);
  }
}
